.download_status{
    margin-top: 100px;
    background-color: #888888; /* Замени на желаемый серый цвет */
    border-radius: 50px;
    border: none;
    color: #eeeeee;
    cursor: pointer;
    position: absolute;
    font-size: 14px;
    padding: 5px 5px;
    text-align: center;
    text-decoration: none;

   }
   @keyframes glowing {
    0% {background: linear-gradient(90deg, rgba(116,115,115,1) 0%, rgba(171,169,169,1) 0%, rgba(116,115,115,1) 100%) }
  10% { background: linear-gradient(90deg, rgba(116,115,115,1) 0%, rgba(171,169,169,1) 10%, rgba(116,115,115,1) 100%) }
  20% { background: linear-gradient(90deg, rgba(116,115,115,1) 0%, rgba(171,169,169,1) 20%, rgba(116,115,115,1) 100%)}
  30% {background: linear-gradient(90deg, rgba(116,115,115,1) 0%, rgba(171,169,169,1)  30%, rgba(116,115,115,1) 100%)}
  40% {background: linear-gradient(90deg, rgba(116,115,115,1) 0%, rgba(171,169,169,1) 40%, rgba(116,115,115,1) 100%)}
  50% { background: linear-gradient(90deg, rgba(116,115,115,1) 0%, rgba(171,169,169,1) 50%, rgba(116,115,115,1) 100%) }
  60% { background: linear-gradient(90deg, rgba(116,115,115,1) 0%, rgba(171,169,169,1) 60%, rgba(116,115,115,1) 100%) }
  70% {background: linear-gradient(90deg, rgba(116,115,115,1) 0%, rgba(171,169,169,1) 70%, rgba(116,115,115,1) 100%) }
  80% {background: linear-gradient(90deg, rgba(116,115,115,1) 0%, rgba(171,169,169,1) 80%, rgba(116,115,115,1) 100%) }
  90% {background: linear-gradient(90deg, rgba(116,115,115,1) 0%, rgba(171,169,169,1) 90%, rgba(116,115,115,1) 100%) }
  100% {background: linear-gradient(90deg, rgba(116,115,115,1) 0%, rgba(171,169,169,1) 100%, rgba(116,115,115,1) 100%)}
}

   .download_status {
  animation: glowing 1300ms infinite;
   }


