.error_boundary-background {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;  
    width: 100vw;
    text-align: center;
    background-color: rgb(236, 50, 87);
}

.error_boundary {
    display: flex;
    flex-direction: column;
    background-color: rgb(255, 255, 255);
    border-radius: 10px;
    color: #020202;
    margin: 0;
    z-index: 9999;
}

.error-boundary_text {
    color: rgb(17, 0, 255);
    font-size: 24px;
    margin-bottom: 10px;
}